	module.exports = {"data":{"markdownRemark":{"html":"<p>Salut! Comme vous deja le savez, je suis en train d'apprendre le français. C'est tout a fait une trés longue route, mais toujours amusante. Peut-etre que mon moyen préféré de pratiquer est d'ecouter des podcasts, étant donné que le listening est ma competence la plus faible en n'importe quelle langue.</p>\n<!--more-->\n<p>Oui, en general, les gens peuvent comprendre assez bien, mais ils ne se sentent pas a l'aise de parler. Cela n'est pas mon cas. De toute façon, je vous donne ma liste personelle et vous souhaite une bonne apprentissage. Il fault remarquer qu'ils ne sont pas tous des podcasts pour des etudiants des langues: il y a aussi des sujets qui m'interessent, comme la technologie et la musique. Avez-vous des suggestions? Laissez-moi des commentaires e je les ajouterai a la liste. ☺️ A la prochaine!</p>\n<p>Disclaimer: comme mon ordinateur a le clavier american, je manque d'accents.</p>\n<h3>Facile</h3>\n<ul>\n<li><a href=\"https://itunes.apple.com/ca/podcast/french-for-beginners/id582330874?mt=2\">French for Beginners</a> (parfait pour ceux que savent qu'un seul mot)</li>\n<li><a href=\"https://itunes.apple.com/ca/podcast/coffee-break-french-two-minute-challenge/id926980081?mt=2\">Coffee Break French</a></li>\n</ul>\n<h3>Intermediaire</h3>\n<ul>\n<li><a href=\"https://itunes.apple.com/ca/podcast/podcast-francais-authentique/id500549470?mt=2\">Français Authentique</a> (tellement bon, je l'ecoute presque tous les jours)</li>\n<li><a href=\"https://itunes.apple.com/ca/podcast/learn-french-with-french-podcasts-fran%C3%A7ais-avec-pierre/id972673390?mt=2\">Français Avec Pierre</a></li>\n<li><a href=\"https://itunes.apple.com/ca/podcast/french-podcast/id427774337?mt=2\">News in Slow French</a></li>\n</ul>\n<h3>Avancé</h3>\n<ul>\n<li><a href=\"https://itunes.apple.com/ca/podcast/scepticisme-scientifique/id322099929?mt=2\">Scepticisme Scientifique</a></li>\n<li><a href=\"https://itunes.apple.com/ca/podcast/au-c%C5%93ur-de-lhistoire-de-franck-ferrand/id423534806?mt=2\">Au Coeur de l'Histoire</a></li>\n<li><a href=\"https://itunes.apple.com/ca/podcast/la-t%C3%AAte-au-carr%C3%A9/id294060079?mt=2\">La Tete au Carré</a></li>\n<li><a href=\"https://itunes.apple.com/ca/podcast/le-ruby-nouveau/id1211857817?mt=2\">Le Ruby Nouveau</a> (developpement de logiciel en general)</li>\n<li><a href=\"https://itunes.apple.com/ca/podcast/aujourdhui-lhistoire/id1040170456?mt=2\">Aujourd'hui L'Histoire</a> (quebecois)</li>\n<li><a href=\"http://feeds.soundcloud.com/users/soundcloud:users:328302816/sounds.rss\">Developpeur de Vie</a></li>\n</ul>\n<h3>Les quebecois a toute vitesse (plus difficile)</h3>\n<ul>\n<li><a href=\"https://itunes.apple.com/ca/podcast/le-f-ing-podcast/id1153019627?mt=2\">Le Fucking Podcast</a></li>\n<li><a href=\"https://itunes.apple.com/ca/podcast/le-podcast-du-tabarnac/id1094569432?mt=2\">Le Podcast du Tabarnac</a></li>\n</ul>","frontmatter":{"date":null,"path":"/firstq","title":"[Français] Podcasts"}}},"pathContext":{}}

